<template>
  <div class="verify_detail">
    <div class="header">
      <div class="title">{{type == 1 ? '个人信息': '企业信息'}}</div>
      <div class="status" v-if="verifyStatus == 1">
        <span class="icon">O</span>
        已通过认证
      </div>
    </div>
    <div class="info">
      <commonTitle txt="基本信息"></commonTitle>
      <div class="content">
        <!-- {{userObj}} -->
        <itemInfo class="item_info" :label="'真是姓名'" :value="userObj.user_name"></itemInfo>
        <itemInfo class="item_info" :label="'性别'" :value="userObj.sex"></itemInfo>
        <itemInfo class="item_info" :label="'联系邮箱'" :value="userObj.email"></itemInfo>
        <itemInfo class="item_info" :label="'证件类型'" :value="userObj.idcardType"></itemInfo>
        <itemInfo class="item_info" :label="'证件号码'" :value="userObj.idcard"></itemInfo>
        <itemInfo class="item_info" :label="'联系地址'" :value="userObj.address"></itemInfo>
      </div>
    </div>
    <div class="attachment">
      <commonTitle txt="附件信息"></commonTitle>
    </div>
  </div>
</template>

<script>
import commonTitle from '@/components/common-title/title'
import itemInfo from '@/components/item-info/item-info'
export default {
  name: 'verify_detail',
  components: {commonTitle, itemInfo},
  data () {
    return {
      id: this.$route.query.id,
      type: this.$route.query.userType,
      verifyStatus: 1,
      userObj: null
    }
  },
  mounted () {
    this.getDetail(this.id, this.type)
  },
  methods: {
    // 根据ID获取详情
    getDetail(id, type) {
      let params = {
        registerId: id,
        registerType: type
      }
      this.commonApi.getVerifyDetail(params)
        .then(res => {
          console.log(res)
          this.userObj = res.data[0]
        })
        .catch(err => {
          this.$Message.error({
            content: err.message,
            duration: 5
          })
        })
    }
  }
}
</script>
<style lang="less" scoped>
.verify_detail {
  .header {
    display: flex;
    .title {
      font-size:18px;
      font-family:PingFangSC-Medium,PingFang SC;
      font-weight:500;
      color: #708094;
    }
    .status {
      padding-top: 6px;
      margin-left: 10px;
      font-size:12px;
      font-family:PingFangSC-Semibold,PingFang SC;
      font-weight:600;
      color: #F5A623;
    }
  }
  .info {
    width:100;
    background: #fff;
    .content {
      display: flex;
      flex-wrap: wrap;
      padding: 10px;
      // height: 100px;
      .item_info {
        min-width: 33%;
      }
    }
  }
  .attachment {
    margin-top: 20px;
    width:100;
    background: #fff;
  }
}
</style>